div.header{
    text-align: center;
    padding: 30px 0;
    border-bottom: 3px solid #fff;

    h1{
        font-size: 2.75em;
    font-weight: 700;
    text-shadow: 0 2px 2px rgba(0,0,0,.4);
    margin-bottom: 15px;
    margin-top: 0px;
    }
    p{
        font-size: .95em;
    color: #ccc;
    width: 650px;
    margin: 0 auto;
    line-height: 21px;
    margin-bottom: 30px;
    }
    strong{
        color: #428bca;
    }
    a {color: #fff; text-decoration: none; outline: none}

    a:hover, a:active {text-decoration: none; color:#fff; }
   
}