$color: #000000;
$alpha: 0.5;

*{
    margin: 0;
    padding: 0;
    background-color: #171717;
    box-sizing: border-box;
}

.wrap{
    max-width: 1170px;
    margin: 0 auto;
    background:#000;
    color: #fff;
}

.cf:after{
    display: block;
    content: '';
    clear: both;
}

