div.footer{
    text-align: center;
    padding: 30px 0;
    border-bottom: 3px solid #fff;

    .p1{
        padding-top: 10px;
    display: inline-block;
        min-height: 1px;
        padding-left: 15px;
        padding-right: 15px;
        display: inline;
    margin-right: 5px;

        a{
            font-size: 0.9em;
        font-weight: bold;
        color: #919191;
        text-decoration: none !important;
        }

        a:hover{
            color: #fff;
        }

    }
    .p2{
         padding-top: 10px;
        min-height: 3px;
        padding-left: 15px;
        padding-right: 15px;
        margin-right: 5px;

            a{
                z-index: 999999999999;
                font-size: 0.9em;
            font-weight: bold;
            color: #919191;
            text-decoration: none !important;
            }

            a:hover{
                color: #fff;
            }

    }
    a {color: #fff; text-decoration: none; outline: none}
    
    .p3{
            min-height: 1px;
            padding-left: 15px;
            padding-right: 15px;
        display: inline-block;
        margin-top: 10px;
        margin-right: 5px;
    a{
        box-sizing: border-box;
    }
    }


}